        <div class="action-buttons mt-5">
          <button class="btn btn-primary" @click="sendSms">Î©îÏãúÏßÄ Î∞úÏÜ°</button>
          <button class="btn btn-secondary" @click="reset">ÏûëÏÑ± Ï∑®ÏÜå</button>
        </div>
      </div>
    </div>

    <div v-if="showModal" class="modal">
      <div class="modal-content">
        <span class="close" @click="closeModal">&times;</span>
