          >
            <input
              type="checkbox"
              :value="option.value"
              :checked="selectedFilter === option.value"
              @change="handleFilterChange(option.value)"
            />
            <span class="custom-checkbox"></span>
            <span class="checkbox-label">{{ option.label }}</span>
          </label>
        </div>
        <input
          v-model="searchQuery"
          @input="searchEmployees"
          placeholder="ÏßÅÏõê Ïù¥Î¶Ñ ÎòêÎäî Î≤àÌò∏Î•º Í≤ÄÏÉâÌï¥Ï£ºÏÑ∏Ïöî."
        />
        <div class="user-list">
          <ul>
            <li
              v-for="employee in searchResults"
              :key="employee.user_no"
              @click="addReceiver(employee)"
              :class="{ selected: isSelected(employee) }"
            >
              {{ employee?.user_no }} - {{ employee?.user_name }}
              <span v-if="isSelected(employee)" class="checkmark">‚úì</span>
            </li>
          </ul>
        </div>
        <button @click="closeModal" class="btn btn-primary mt-3">ÏôÑÎ£å</button>
      </div>
    </div>
