</template>

<script>
import api from "@/api/axios";

export default {
  name: "MileagePromotion",
  props: ["mile_name"],
  data() {
    return {
      message: "",
      receivers: [],
      showModal: false,
      searchQuery: "",
      searchResults: [],
      selectedFilter: null,
      filterOptions: [
        { label: "Ï†ÑÏ≤¥", value: "all" },
        { label: "ÏòÅÏóÖÏ†ê", value: "branch" },
        { label: "Î≥∏Î∂Ä", value: "headquarters" },
      ],
      employees: [],
      sampleMessages: [
        {
          title: "Ïã†Í∑ú Î™©Ìëú ÏïàÎÇ¥",
          content: `${this.mile_name} ÎßàÏùºÎ¶¨ÏßÄÏùò Ïã†Í∑ú Î™©ÌëúÍ∞Ä ÏÉùÏÑ± ÎêòÏóàÏäµÎãàÎã§ü§ó. ÎßàÏôï ÏÑ†Ï†ïÏóê ÎèÑÏõÄÏù¥ ÎêòÎäî Î™©Ìëú ÏÑ§Ï†ïüéØ ÏßÄÍ∏à Ìï®Íªò Îñ†ÎÇòÎ≥ºÍπåÏöî?`,
        },
        {
          title: "M-Tip Î∞©Î¨∏ Ï†úÏïà",
          content: `${this.mile_name} ÎßàÏùºÎ¶¨ÏßÄ, M-Tip Í≤åÏãúÌåêÏóêÏÑú ÏßÅÏõêÎì§Í≥º üçØÍøÄÌåÅÏùÑ Í≥µÏú†Ìï¥Î≥¥ÏÑ∏Ïöî! ÎãπÏã†ÎèÑ ÎßàÏùºÎ¶¨ÏßÄ ÏôïÏúºÎ°ú Í±∞Îì≠ÎÇ†Ïàò ÏûàÏñ¥Ïöî!`,
        },
        {
          title: "ÎèôÍ∏∞ Î∂ÄÏó¨",
          content: `ÌòÑÏû¨ Îã§Î•∏ÏßÅÏõêÎì§Ïùò ${this.mile_name} ÎßàÏùºÎ¶¨ÏßÄ Í¥ÄÏã¨ÎèÑÍ∞Ä ÎÇÆÏïÑÏ°åÏñ¥Ïöî! Ïù¥ÎïåÍ∞Ä Í∏∞ÌöåÏòàÏöî! ÏñºÎ•∏ Ïû°ÏïÑÎ≤ÑÎ¶¨ÏÑ∏Ïöî üßê`,
        },
      ],
    };
  },
  methods: {
    selectSampleMessage(sample) {
      this.message = sample.content;
    },
    async generateAIContent() {
      console.log("AI ÎÇ¥Ïö© ÏÉùÏÑ±");
      this.AIAlret()
      try {
        const response = await api.post('bot/sms', null, {
          params: {
            prompt:
              `Ïö∞Î¶¨ ÌöåÏÇ¨ Ïù∏ÏÇ¨Í≥†Í≥ºÏôÄ Ïó∞Í≤∞ÎêòÎäî ÎßàÏùºÎ¶¨ÏßÄ Í¥ÄÎ¶¨ ÏÇ¨Ïù¥Ìä∏ÏóêÏÑú ${this.mile_name} ÎßàÏùºÎ¶¨ÏßÄÏóê Í¥ÄÌï¥ÏÑú ÌôçÎ≥¥ÌïòÍ≥† Î∞©Î¨∏ÏùÑ ÎèÖÎ†§ÌïòÎ†§Í≥† ÌôçÎ≥¥ÏÑ± Î¨∏ÏûêÎ©îÏãúÏßÄÎ•º Î≥¥ÎÇ¥Î†§Í≥†Ìï¥.` +
              'Í∑∏ ÌôçÎ≥¥ÌïòÎäî Î¨∏Ïûê Î©îÏãúÏßÄ Î¨∏Íµ¨Î•º ÎßåÎì§Ïñ¥Ï£ºÎ©¥ ÎêòÎäîÎç∞, ÎßàÏùºÎ¶¨ÏßÄÎäî Ïù¥Î≤§Ìä∏ÏÑ±ÏúºÎ°ú Ï¢ÖÏ¢Ö ÏÉùÏÑ±ÎêòÎäî Í≥µÎèôÎ™©ÌëúÏóê Ï∞∏Ïó¨Ìï¥ÏÑú Îã¨ÏÑ±ÌïòÎ©¥ ÎøåÎìØÌï®ÏùÑ ÏñªÍ∏∞ÎèÑÌïòÍ≥†, ' +
              'ÎßàÏùºÎ¶¨ÏßÄÍ∞Ä Ï†ÑÏõîÏóêÎπÑÌï¥ ÌÅ¨Í≤å Ï¶ùÍ∞ÄÌñàÍ±∞ÎÇò, ÎßàÏùºÎ¶¨ÏßÄÎ∂ÄÎ¨∏ÏóêÏÑú 1Îì±ÏùÑÌïòÎ©¥ ÎßàÏôï(ÎßàÏùºÎ¶¨ÏßÄÏôï)Ïù¥ÎùºÎäî Î∞∞ÏßÄÎ•º ÌöçÎìùÌïòÏó¨ ÎøåÎìØÌï®ÏùÑ ÎäêÎÇÑÏàò ÏûàÍ∏∞ÎèÑÌï¥. Ïù¥ ÏÇ¨Ïù¥Ìä∏Ïùò Ï∂úÏÑùÏ≤¥ÌÅ¨ ÎòêÎäî ÏßÅÏõêÎì§Í≥º ÎßàÏùºÎ¶¨ÏßÄ ÍøÄÌåÅÏùÑ Í≥µÏú†ÌïòÎäî M-TipÍ≤åÏãúÌåêÏùò Î≤†Ïä§Ìä∏ Í∏ÄÎ°ú ÏÑ†Ï†ïÎêòÎ©¥ ÎßàÏôï ÏÑ†Ï†ïÏóê Í∞ÄÏ†êÏù¥ ÏûàÏñ¥. ÌïòÏßÄÎßå Í∞ÄÏû• Ï§ëÏöîÌïúÍ≤ÉÏùÄ Ïù∏ÏÇ¨Í≥†Í≥ºÏóê Î∞òÏòÅÎêúÎã§ÎäîÍ±∞Ïïº.  ' +
              `ÎÇ¥Í∞Ä ÏòàÏãúÎ°ú ÏßÅÏ†ë ÎßåÎì† Î¨∏ÏûêÎ¨∏Íµ¨Î•º Î™áÍ∞ú Ï§ÑÍ≤å! ${this.mile_name} ÎßàÏùºÎ¶¨ÏßÄÏùò Ïã†Í∑ú Î™©ÌëúÍ∞Ä ÏÉùÏÑ± ÎêòÏóàÏäµÎãàÎã§. ÎßàÏôï ÏÑ†Ï†ïÏóê ÎèÑÏõÄÏù¥ ÎêòÎäî Î™©Ìëú ÏÑ§Ï†ï ÏßÄÍ∏à Ìï®Íªò Îñ†ÎÇòÎ≥ºÍπåÏöî?,${this.mile_name} ÎßàÏùºÎ¶¨ÏßÄ, M-Tip Í≤åÏãúÌåêÏóêÏÑú ÏßÅÏõêÎì§Í≥º ÍøÄÌåÅÏùÑ Í≥µÏú†Ìï¥Î≥¥ÏÑ∏Ïöî! ÎãπÏã†ÎèÑ ÎßàÏùºÎ¶¨ÏßÄ ÏôïÏúºÎ°ú Í±∞Îì≠ÎÇ†Ïàò ÏûàÏñ¥Ïöî!,ÌòÑÏû¨ Îã§Î•∏ÏßÅÏõêÎì§Ïùò ${this.mile_name} ÎßàÏùºÎ¶¨ÏßÄ Í¥ÄÏã¨ÎèÑÍ∞Ä ÎÇÆÏïÑÏ°åÏñ¥Ïöî! Ïù¥ÎïåÍ∞Ä Í∏∞ÌöåÏòàÏöî! ÏñºÎ•∏ Ïû°ÏïÑÎ≤ÑÎ¶¨ÏÑ∏Ïöî üßê `+
              'Íº≠ Ïù¥Îü∞ Î©òÌä∏Í∞Ä Îì§Ïñ¥Í∞ÄÏßÄÏïäÏïÑÎèÑ ÎßàÏùºÎ¶¨ÏßÄÏóê Í¥ÄÏã¨Ïù¥ ÎèãÍ±∞ÎÇò ÎèÖÎ†§ÌïòÎäî Î¨∏Ïû•ÏúºÎ°úÎèÑ Ï∂©Î∂ÑÌï¥. 100ÏûêÏù¥ÎÇ¥Î°ú ÌïòÎÇòÏùò Î©îÏãúÏßÄÎßå ÏÉùÏÑ± Î∂ÄÌÉÅÌï†Í≤å. Î∞îÎ°ú Î¨∏ÏûêÎ©îÏãúÏßÄÎ•º Î∞úÏÜ°Ìï†Í±∞ÎãàÍπå  ÌïòÎÇòÏùò Î¨∏ÏûêÎ©îÏãúÏßÄÎÇ¥Ïö© Í∑∏ÎåÄÎ°úÎ•º Ï§ò.'
          },
        });
        console.log(response.data);
        this.message = response.data;
      } catch (error) {
        console.error('Error during API request:', error);
      }
    },
    warningAlert(alret) {
      this.$swal({
        title: 'Í≤ΩÍ≥†',
        text: alret,
        icon: 'warning',
        scrollbarPadding: false,
      });
    },
    reset() {
      this.message = "";
      this.receivers = [];
    },
    openModal() {
      this.showModal = true;
      this.searchResults = this.employees;
      this.selectedFilter = null; // ÌïÑÌÑ∞ Ï¥àÍ∏∞Ìôî
      this.searchQuery = "";
    },
    closeModal() {
      this.showModal = false;
      this.searchQuery = "";
      this.searchResults = [];
    },
    searchEmployees() {
      this.filterEmployees(); // ÌïÑÌÑ∞ Ï†ÅÏö© ÌõÑ Í≤ÄÏÉâ ÏàòÌñâ
    },
    addReceiver(employee) {
    const index = this.receivers.findIndex(
      (r) => r.user_no === employee.user_no
    );
    if (index === -1) {
      this.receivers.push(employee);
    } else {
      this.receivers.splice(index, 1);
    }
  },

    isSelected(employee) {
      return this.receivers.some((r) => r.user_no === employee.user_no);
    },
    handleFilterChange(value) {
    if (this.selectedFilter === value) {
      this.selectedFilter = null;
      this.receivers = []; // ÏÑ†ÌÉù Ìï¥Ï†ú Ïãú receivers Ï¥àÍ∏∞Ìôî
    } else {
      this.selectedFilter = value;
      this.addEmployeesByFilter(value); // ÌïÑÌÑ∞ ÏÑ†ÌÉù Ïãú ÏßÅÏõê Ï∂îÍ∞Ä
    }
    this.filterEmployees(); // Í≤ÄÏÉâ Í≤∞Í≥º ÏóÖÎç∞Ïù¥Ìä∏
  },
  addEmployeesByFilter(filter) {
    if (filter === "all") {
      this.receivers = [...this.employees];
    } else if (filter === "branch") {
      this.receivers = this.employees.filter(
        (emp) => emp.job_no == 1 || emp.job_no == 2
      );
    } else if (filter === "headquarters") {
      this.receivers = this.employees.filter((emp) => emp.job_no == 3);
    }
  },

    removeEmployeesByFilter(filter) {
      if (filter === "all") {
        this.receivers = [];
      } else if (filter === "branch") {
        this.receivers = this.receivers.filter(
          (emp) => emp.job_no !== 1 && emp.job_no !== 2
        );
      } else if (filter === "headquarters") {
        this.receivers = this.receivers.filter((emp) => emp.job_no !== 3);
      }
    },
    filterEmployees() {
    let filteredEmployees = this.employees;

    // ÌïÑÌÑ∞ Ï†ÅÏö©
    if (this.selectedFilter === "branch") {
      filteredEmployees = filteredEmployees.filter(
        (emp) => emp.job_no == 1 || emp.job_no == 2
      );
    } else if (this.selectedFilter === "headquarters") {
      filteredEmployees = filteredEmployees.filter((emp) => emp.job_no == 3);
    }

    // Í≤ÄÏÉâÏñ¥ Ï†ÅÏö©
    if (this.searchQuery) {
      const lowercaseSearchTerm = this.searchQuery.toLowerCase();
      filteredEmployees = filteredEmployees.filter((user) => {
        return (
          user.user_name.toLowerCase().includes(lowercaseSearchTerm) ||
          user.user_no.toString().includes(this.searchQuery)
        );
      });
    }

    // searchResults ÏóÖÎç∞Ïù¥Ìä∏
    this.searchResults = filteredEmployees;
  },

    applySearch() {
      if (this.searchQuery) {
        const lowercaseSearchTerm = this.searchQuery.toLowerCase();
        this.searchResults = this.searchResults.filter((user) => {
          return (
            user.user_name.toLowerCase().includes(lowercaseSearchTerm) ||
            user.user_no.toString().includes(this.searchQuery)
          );
        });
      }
    },

    //Î™®Îì† Ïú†Ï†Ä Í∞ñÍ≥†Ïò§Í∏∞
    async getAllUsers() {
      try {
        const response = await api.get("/user/getAllUsers");
        this.employees = response.data;
        console.log("Î™®Îì†ÏßÅÏõêÏ†ïÎ≥¥ :", response.data);
      } catch (error) {
        console.error("Error getAllUsers");
      }
    },
    succesAlert() {
      this.$swal({
        title: "ÏÑ±Í≥µ",
        text: "Î©îÏãúÏßÄ Î∞úÏÜ°Ïù¥ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.",
        icon: "success",
        scrollbarPadding: false,
      });
    },
    async sendSms() {

      
      if (this.message.trim().length === 0) {
        this.warningAlert('Î©îÏãúÏßÄÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
        return;
      }

      if (this.receivers.length === 0) {
        this.warningAlert('ÏàòÏã†ÏûêÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
        return;
      }


      
      try {
        let receiversPhone = this.receivers.map((r) => r.user_tel);
        const response = await api.post("/user/sendSms", {
          to: receiversPhone,
          text: this.message,
          mile : this.mile_name
        });
        console.log(response);
        this.succesAlert();
        this.reset();
      } catch (error) {
        console.error("Error sending SMS:", error);
        this.errorAlert(error.message || "Î©îÏãúÏßÄ Ï†ÑÏÜ° Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
      }
    },
    AIAlret() {
    let timerInterval;
    let timeLeft = 10; 
    
    this.$swal({
      title: "AI Î¨∏Íµ¨ ÏÉùÏÑ±",
      html: "<b>${timeLeft}</b>Ï¥à ÌõÑÏóê ÏôÑÎ£åÎê©ÎãàÎã§....",
      timer: 10000,
      timerProgressBar: true,
      scrollbarPadding: false,
      
      didOpen: () => {
        const popup = this.$swal.getPopup();
        popup.style.height = '200px'; // ÏõêÌïòÎäî ÎÜíÏù¥Î°ú Ï°∞Ï†ï

        this.$swal.showLoading();
        const timer = this.$swal.getHtmlContainer().querySelector("b");
        timer.textContent = `${timeLeft}`; // Ï¥àÍ∏∞ ÏãúÍ∞Ñ ÏÑ§Ï†ï
        timerInterval = setInterval(() => {
          timeLeft -= 1;
          timer.textContent = `${timeLeft}`;
          if (timeLeft === 0) {
            clearInterval(timerInterval);
          }
        }, 1000); // 1Ï¥àÎßàÎã§ ÏóÖÎç∞Ïù¥Ìä∏
      },
      willClose: () => {
        clearInterval(timerInterval);
      },
    }).then((result) => {
      if (result.dismiss === this.$swal.DismissReason.timer) {
        console.log("I was closed by the timer");
      }
    });
   }
  },

  computed: {
    receiverDisplay() {
      const count = this.receivers.length;
      if (count === 0) {
        return "";
      } else if (count <= 3) {
        return this.receivers.map((r) => r.user_name).join(", ");
      } else {
        const firstThree = this.receivers
          .slice(0, 3)
          .map((r) => r.user_name)
          .join(", ");
        return `${firstThree} Ïô∏ ${count - 3}Î™Ö`;
      }
    },
  },

  mounted() {
    this.getAllUsers();
  },
};
</script>

